 }
 .imagen{
    width: calc(10vw + 10vh + 20px);
}
.toggle-logo{
    display: none;
    width: calc(5vw + 2vh + 20px);
}
 @media screen and (max-width: 768px){
     .notToggle{
         display: none;
     }
     .toggleMenu{
